var that;
export default {
	data() {
		return {
			zhanghao: '',
			mima: ''

		}
	},
	onLoad: function(e) {
		that = this;
		
	},
	onShow: function(res) {

	},
	methods: {
		zhuce: function() {
			uni.navigateTo({
				url: './zhuce'
			})
		},
		wangjimima: function() {
			uni.navigateTo({
				url: './wangjimima'
			})
		},
		lijidenglu: function() {
			if(!that.zhanghao){
				uni.showToast({
					title:'请输入账号'
				})
				
				return
			}
			if(!that.mima){
				uni.showToast({
					title:'请输入密码'
				})
				
				return
			}
			try {
				uni.setStorageSync('token', '1234567890');
				uni.navigateBack({
					delta: 1
				})
			} catch (e) {
				// error
			}

		}
	}

}
